import React from "react"
import { Link } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

export default function ColumnLink({ column }) {
  const { title, updatedAt } = column
  const excerpt = column.excerpt.excerpt
  const pageLink = `/column/${column.slug}`
  const image = getImage(column.image)

  return (
    <Link to={pageLink}>
      <div>
          <div>
              <GatsbyImage image={image} alt={column.title}/>
          </div>
          <div>
              <h2>{title}</h2>
              <p>{excerpt}</p>
              <p>{updatedAt}</p>
          </div>
      </div>
    </Link>
  )
}